.form {
  margin-bottom: 2rem;
}

.lost {
  display: inline-block;
  color: #666;
  padding: .5rem 0;
  line-height: 1;
}

.lost::after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: currentColor;
  display: block;
}

.create {
  margin-top: 4rem;
  margin-bottom: 4rem;
}

.create > p {
  margin:1rem 0 2rem 0;
}

.subtitle {
  font-family: var(--type-second);
  line-height: 1;
  font-size: 2rem;
}

.subtitle::after {
  content: "";
  display: block;
  background-color: #fb1;
  height: .5rem;
  width: 3rem;
  border-radius: .2rem;
}